const popupOverlay = document.getElementById('popupOverlay');
const popupContent = document.getElementById('popupContent');

const showPopup = (data) => {
  // Set the content of the popup
  data.forEach((e) => {
    popupContent.innerHTML = `
    <span class="popup-close">&times;</span>
    <img id="api-image" src="${e.image.original}" />
    <p class="show-name">${e.name}</p>
    <div class="likes">
        <i class="uil uil-heart-alt"></i>
        <span>${e.likes} likes</span>
    </div>              
    <button class="comment">Comments</button>
    <button class="reservations">Reservations</button>
    `;

  popupOverlay.style.display = 'flex';
  });
};

export const openComment = (e) => {
  document.addEventListener('click', (event) => {
    if (event.target.id === `${e.name}`) {
      showPopup(e);
    }
  });
};

document.addEventListener('click', (event) => {
  const closeButton = event.target.closest('.popup-close');
  if (closeButton) {
    popupOverlay.style.display = 'none';
  }
});

export default showPopup;